<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-company-inventaris-assets',
  data () {
    return {
      config: {
        title: 'Asset Inventaris Perusahaan',
        model_api: 'company-inventaris-assets',
        getter: 'company-inventaris-assets',
        setter: 'company-inventaris-assets',
        pk_field: null,
        permission: {
          create: 'create-company-inventaris-assets',
          read: 'view-company-inventaris-assets',
          update: 'update-company-inventaris-assets',
          delete: 'delete-company-inventaris-assets'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'asset_name', label: 'Nama Aset' },
          { id: 'asset_code', label: 'Kode Aset' },
          { id: 'asset_qty', label: 'Jumlah Aset' },
          { id: 'faktur_number', label: 'Nomor Faktur' },
          { id: 'serial_number', label: 'Nomor Serial' },
          { id: 'pic', label: 'PIC' },
          { id: 'current_location', label: 'Lokasi Sekarang', placeholder: 'Nama Kota, Daerah, atau Proyek' },
          { id: 'asset_condition', label: 'Kondisi Aset', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'price_purchased', label: 'Harga Beli', methods: { list: { transform: 'money' }, detail: { transform: 'money' }, create: { type: 'money' }, update: { type: 'money' } } },
          { id: 'date_purchased', label: 'Tanggal Pembelian', methods: { list: { transform: 'longDate' }, detail: { transform: 'longDate' }, create: { type: 'date' }, update: { type: 'date' }, filter: false } },
          {
            id: 'inventaris_asset_type_id',
            label: 'Tipe Asset',
            methods: {
              create: {
                setter: 'master-inventaris-asset-types',
                getter: 'master-inventaris-asset-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-inventaris-asset-types',
                getter: 'master-inventaris-asset-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-inventaris-asset-types',
                getter: 'master-inventaris-asset-types',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'img_photo_asset',
            label: 'File Aset',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'img_photo_asset.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }],
                view_data: 'img_photo_asset'
              }
            }
          }
        ]
      }
    }
  }
}
</script>



